.lg__title{
    color:$secondary-color;
    margin-bottom: 60px;
    text-align: center;
    font-size: 48px;
    @include pc-1024{
        font-size: 32px;
        margin-bottom: 32px;
    }
    @include phone-576{
        font-size: 20px;
        margin-bottom: 20px;
    }
}

