@media (max-width: 1024px) {
    .text-md-sm{
        font-size: $base-font-size*0.875;
    }
    .text-md-base{
        font-size: $base-font-size;
    }
    .text-md-lg{
        font-size: $base-font-size*1.125;
    }
    .text-md-xl{
        font-size: $base-font-size*1.25;
    }
    .text-md-2xl{
        font-size: $base-font-size*1.5;
    }
    .text-md-3xl{
        font-size: $base-font-size*1.75
    }
    .text-md-4xl{
        font-size: $base-font-size*2;
    }
    .text-md-5xl{
        font-size: $base-font-size*3;
    }
}

@media (max-width: 576px) {
    .text-sm-xs{
        font-size: $base-font-size*0.75;
    }
    .text-sm-sm{
        font-size: $base-font-size*0.875;
    }
    .text-sm-base{
        font-size: $base-font-size ;
    }
    .text-sm-lg{
        font-size: $base-font-size*1.25 ;
    }
    .text-sm-xl{
        font-size: $base-font-size*1.5 ;
    }
    .text-sm-2xl{
        font-size: $base-font-size*1.75;
    }
    .text-sm-3xl{
        font-size: $base-font-size*2;
    }
}
/* text color */
.text-white{
    color:#ffffff;
}
.text-black{
    color:#000;
}
.text-primary{
    color:$primary-color;
}
.text-secondary{
    color:$secondary-color;
}
.text-sm-secondary{
    color:$secondary-color;
}

/* text align */

.text-left{	
    text-align: left;
}
.text-center{
    text-align: center;
}
.text-right{
    text-align: right;
}


/* font weight */


.font-normal{
    font-weight: normal;
}
.font-medium{
    font-weight: 500;
}
.font-bold{
    font-weight: 700;
}
.font-black{
    font-weight: 900;
}

/* text style */

.italic{
    font-style: italic;
}


/* line-height */

.lh-1{
    line-height: 1;
}
.lh-2{
    line-height: 1.2;
}
